<template>
    <div class="aside">
        <ul>
            <li> 
                <a href="#" @mouseover="wxshow = !wxshow" @mouseleave="wxshow = !wxshow"><img :src="picData[0]"   alt="合作" />微信</a>
                <div class="weixin" v-if="wxshow">
                    <p>微信</p>
                    <img :src="weixinimg" />
                </div>
            </li>
            <li class="consulting">
                <a href="#this"><span></span><span></span><span></span>
                    <img :src="picData[1]" class="img1" alt="建站在线客服" />
                    <img :src="picData[3]" class="img2" alt="建站在线客服" />QQ
                </a>
            </li>
            <li> 
                <a href="#"  @mouseover="tellshow = !tellshow" @mouseleave="tellshow = !tellshow"><img :src="picData[2]"  />电话</a> 
                <div class="tell" v-if="tellshow">
                    <p>电话：<span>13691311485</span></p>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>

export default {
    data() {
        return {
            picData: [
                require('../../assets/images/057.png'),
                require('../../assets/images/059.png'),
                require('../../assets/images/060.png'),
                require('../../assets/images/061.png')
            ],
            weixinimg:require('../../assets/images/weixin.png'),
            wxshow:false,
            tellshow:false,
        }
    }
}
</script>
<style lang="less" rel="stylesheet/less" scoped>
    .aside li{
        position: relative;
    }
    .weixin{
        position: absolute;
        padding: 10px;
        left: -140px;
        top: 0px;
        background: #257dff;
    }
    .weixin img{
        width: 120px;
    }
    .weixin p{
        text-align: center;
        font-size: 16px;
        line-height: 25px;
        color: #ffffff;
        margin-bottom: 5px;
    }
    .tell{
        position: absolute;
        left: -215px;
        background: #257dff;
        color: #fff;
        line-height: 48px;
        top: 0px;
        font-size: 16px;
        padding: 0 20px;
        width: 175px;
    }
    .tell span{
        font-size: 18px;
    }
</style>


